.search__root {
  position: relative;
  width: 300px;
}

.search__input {
  border: 1px solid rgba(0, 0, 0, 0.1);
  padding: 8px 20px;
  padding-left: 42px;
  width: 100%;
  border-radius: 5px;
  font-size: 16px;

  &:focus {
    border: 1px solid rgba(0, 0, 0, 0.2);
  }
}

.search__icon {
  width: 22px;
  height: 22px;
  opacity: 0.3;
  position: absolute;
  left: 14px;
  top: 8px;
}
.search__clearIcon {
  width: 18px;
  height: 18px;
  opacity: 0.3;
  position: absolute;
  right: 15px;
  top: 10px;
  cursor: pointer;

  &:hover {
    opacity: 0.8;
  }
}
